<template>
    <img :src="imageSource" alt="Red dot"/>
    <label>{{description}}</label>
</template>

<script>
export default{
    props:{
        imageSource : String,
        description: String
    },
    Data(){
        return{
            img : 'data:image/jpeg;base64,'
        }
    },
    mounted(){
        this.img = this.imageSource
    }
}
</script>
