<template>
    <div :key="post in posts">
        <h1>{{post.description}}</h1>
    </div>
    <image-post-component imageSource="data:image/jpeg;base64," description="hoi"/>
</template>

<script>
import api from '../wrappers/PostWrapper.js'
import ImagePostComponent from '../components/ImagePostComponent.vue'

    export default{
        components:{
            ImagePostComponent
        },
        data(){
            return{
                posts: [],
            }
        },

        async created(){
            this.posts = await api.getPosts();
        }


    }
</script>
